.tree-node circle {
	fill: #fff;
	stroke: steelblue;
	stroke-width: 1.5px;
}

.highlight-node circle {
	fill: rgb(255, 245, 153);
	stroke: orange;
	stroke-width: 1.5px;
}

.tree-node,
.highlight-node {
	font: 10px sans-serif;
}

.hidden-node,
.hidden-link {
	opacity: 0;
}

.link {
	fill: none;
	stroke: #ccc;
	stroke-width: 1.5px;
}

.overview-hover {
	font-size: 10;
	font-weight: normal;
}

.relect-placeholder {
	font-size: 11px;
}

.relect-option {
	font-size: 13px;
}




